___CSS_LOADER_EXPORT___.push([module.id, `header{
    background-color: var(--black);
    position: fixed;
    z-index: 10;
    width: 100%;
    color: var(--lavender);
}
nav{
    background-color: var(--black);
    color: var(--lavender);
    display: flex;
    padding: 1rem 1.5rem;
    align-items: center;
    margin-inline: auto;
    max-width: 1024px;
    justify-content: space-between;
}
nav .brand{
    font-family: 'Croissant One', cursive;
    font-weight: bold;
    font-size: xx-large;
}
nav ul{
    list-style-type: none;
    display: flex;
    justify-content: space-between;
    font-size: small;
    gap: 2rem;
}
nav ul.slide{
    transform: translateY(0);
}
nav ul a{
    text-decoration: none;
    color: var(--lavender);
    padding: 1rem 1rem ;
}
nav button{
    display: none;
    padding: 1rem 1.5rem;
}
@media (max-width: 576px){
    nav ul{
        display: flex;
        gap: 3rem;
        right: 0;
        top: 5rem; 
        width: 100%;
        position: absolute;
        background-color: var(--lavender);
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
        padding-block: 2rem;
        z-index: -1;
        transform: translateY(-100%);
        transition: all 0.5s;
    }
    nav ul a{
        color: var(--black);
    }
    nav button{
        display: block;
    }
}`, "",{"version":3,"sources":["webpack://./src/styles/header.css"],"names":[],"mappings":"AAAA;IACI,8BAA8B;IAC9B,eAAe;IACf,WAAW;IACX,WAAW;IACX,sBAAsB;AAC1B;AACA;IACI,8BAA8B;IAC9B,sBAAsB;IACtB,aAAa;IACb,oBAAoB;IACpB,mBAAmB;IACnB,mBAAmB;IACnB,iBAAiB;IACjB,8BAA8B;AAClC;AACA;IACI,qCAAqC;IACrC,iBAAiB;IACjB,mBAAmB;AACvB;AACA;IACI,qBAAqB;IACrB,aAAa;IACb,8BAA8B;IAC9B,gBAAgB;IAChB,SAAS;AACb;AACA;IACI,wBAAwB;AAC5B;AACA;IACI,qBAAqB;IACrB,sBAAsB;IACtB,mBAAmB;AACvB;AACA;IACI,aAAa;IACb,oBAAoB;AACxB;AACA;IACI;QACI,aAAa;QACb,SAAS;QACT,QAAQ;QACR,SAAS;QACT,WAAW;QACX,kBAAkB;QAClB,iCAAiC;QACjC,sBAAsB;QACtB,6BAA6B;QAC7B,mBAAmB;QACnB,mBAAmB;QACnB,WAAW;QACX,4BAA4B;QAC5B,oBAAoB;IACxB;IACA;QACI,mBAAmB;IACvB;IACA;QACI,cAAc;IAClB;AACJ","sourcesContent":["header{\r\n    background-color: var(--black);\r\n    position: fixed;\r\n    z-index: 10;\r\n    width: 100%;\r\n    color: var(--lavender);\r\n}\r\nnav{\r\n    background-color: var(--black);\r\n    color: var(--lavender);\r\n    display: flex;\r\n    padding: 1rem 1.5rem;\r\n    align-items: center;\r\n    margin-inline: auto;\r\n    max-width: 1024px;\r\n    justify-content: space-between;\r\n}\r\nnav .brand{\r\n    font-family: 'Croissant One', cursive;\r\n    font-weight: bold;\r\n    font-size: xx-large;\r\n}\r\nnav ul{\r\n    list-style-type: none;\r\n    display: flex;\r\n    justify-content: space-between;\r\n    font-size: small;\r\n    gap: 2rem;\r\n}\r\nnav ul.slide{\r\n    transform: translateY(0);\r\n}\r\nnav ul a{\r\n    text-decoration: none;\r\n    color: var(--lavender);\r\n    padding: 1rem 1rem ;\r\n}\r\nnav button{\r\n    display: none;\r\n    padding: 1rem 1.5rem;\r\n}\r\n@media (max-width: 576px){\r\n    nav ul{\r\n        display: flex;\r\n        gap: 3rem;\r\n        right: 0;\r\n        top: 5rem; \r\n        width: 100%;\r\n        position: absolute;\r\n        background-color: var(--lavender);\r\n        flex-direction: column;\r\n        justify-content: space-evenly;\r\n        align-items: center;\r\n      