___CSS_LOADER_EXPORT___.push([module.id, `:root{
  --gainsboro: #d6d8e5;
  --lavender: #e7eaf3;
  --black: #05080c;
}
*{
  margin: 0; 
  padding: 0;
}

body{
  font-family: "Poppins",sans-serif;
  background-color: var(--gainsboro);
  width: 100%;
}

button{
  border: none;
  padding-inline: 1.5rem;
  padding-block: 1rem;
  border-radius: 1rem;
  font-weight: bold;
}
button.regular{
  color: var(--lavender);
  background-color: var(--black);
}
button.convert{
  color: var(--black);
  background-color: var(--lavender);
}
button.convert:hover{
  background-color: var(--gainsboro);
}

.card{
  display: flex;
  flex-direction: column;
  background-color: var(--lavender);
  border-radius: 1rem;
  box-shadow: 5px 5px 10px #b2b4c1;
  transition: transform 0.2s ease-in-out;
}
.card img{
  width: 100%;
  border-radius: 1rem 1rem 0 0;
  height: 200px;
}
.card .card-desc{
  display: flex;
  flex-direction: column;
  padding-inline: 1.5rem;
  padding-top: .5rem;
  padding-bottom: 2rem;
  gap: .8rem;
}
.card .card-desc h3{
  color: var(--black);
}
.card .card-desc a{
  color: var(--black);
  opacity: .8;
  text-decoration: none;
}
.card .card-desc h3 span{
  font-weight: bold;
  opacity: .8;
  font-size: small;
}
.card .card-desc p.description{
  font-size: smaller;
}
.card:hover{
  transform: scale(1.03);
}
.skip-link {
  position: absolute;
  top: -100px;
  left: 0;
  padding: 1rem;
  background-color: var(--gainsboro);
  color: var(--black);
  z-index: 999;
}
.skip-link:focus{
  top: 0;
}
.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--black);
  
  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;
  
  font-size: 18px;
  color: var(--gainsboro);
  cursor: pointer;
}`, "",{"version":3,"sources":["webpack://./src/styles/main.css"],"names":[],"mappings":"AAOA;EACE,oBAAoB;EACpB,mBAAmB;EACnB,gBAAgB;AAClB;AACA;EACE,SAAS;EACT,UAAU;AACZ;;AAEA;EACE,iCAAiC;EACjC,kCAAkC;EAClC,WAAW;AACb;;AAEA;EACE,YAAY;EACZ,sBAAsB;EACtB,mBAAmB;EACnB,mBAAmB;EACnB,iBAAiB;AACnB;AACA;EACE,sBAAsB;EACtB,8BAA8B;AAChC;AACA;EACE,mBAAmB;EACnB,iCAAiC;AACnC;AACA;EACE,kCAAkC;AACpC;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,iCAAiC;EACjC,mBAAmB;EACnB,gCAAgC;EAChC,sCAAsC;AACxC;AACA;EACE,WAAW;EACX,4BAA4B;EAC5B,aAAa;AACf;AACA;EACE,aAAa;EACb,sBAAsB;EACtB,sBAAsB;EACtB,kBAAkB;EAClB,oBAAoB;EACpB,UAAU;AACZ;AACA;EACE,mBAAmB;AACrB;AACA;EACE,mBAAmB;EACnB,WAAW;EACX,qBAAqB;AACvB;AACA;EACE,iBAAiB;EACjB,WAAW;EACX,gBAAgB;AAClB;AACA;EACE,kBAAkB;AACpB;AACA;EACE,sBAAsB;AACxB;AACA;EACE,kBAAkB;EAClB,WAAW;EACX,OAAO;EACP,aAAa;EACb,kCAAkC;EAClC,mBAAmB;EACnB,YAAY;AACd;AACA;EACE,MAAM;AACR;AACA;EACE,WAAW;EACX,YAAY;EACZ,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,8BAA8B;;EAE9B,eAAe;EACf,YAAY;EACZ,WAAW;EACX,kBAAkB;EAClB,SAAS;;EAET,eAAe;EACf,uBAAuB;EACvB,eAAe;AACjB","sourcesContent":["@import url(./header.css);\r\n@import url(./hero.css);\r\n@import url(./find.css);\r\n@import url(./footer.css);\r\n@import url(./detail.css);\r\n@import url(./add-review.css);\r\n\r\n:root{\r\n  --gainsboro: #d6d8e5;\r\n  --lavender: #e7eaf3;\r\n  --black: #05080c;\r\n}\r\n*{\r\n  margin: 0; \r\n  padding: 0;\r\n}\r\n\r\nbody{\r\n  font-family: \"Poppins\",sans-serif;\r\n  background-color: var(--gainsboro);\r\n  width: 100%;\r\n}\r\n\r\nbutton{\r\n  border: none;\r\n  padding-inline: 1.5rem;\r\n  padding-block: 1rem;\r\n  border-radius: 1rem;\r\n  font-weight: bold;\r\n}\r\nbutton.regular{\r\n  color: var(--lavender);\r\n  background-color: var(--black);\r\n}\r\nbutton.convert{\r\n  color: var(--black);\r\n  background-color: var(--lavender);\r\n}\r\nbutton.convert:hover{\r\n  background-color: var(--gainsboro);\r\n}\r\n\r\n.card{\r\n  display: flex;\r\n  flex-direction: column;\r\n  background-color: var(--lavender);\r\n  border-ra